import { format } from 'date-fns';

function ModalContentOrdersList({order}) {

    const formatDate = (date) => {
        return format(date, 'dd.MM.yy HH:mm');
    };

    const {lastName, firstName, middleName, phoneNumber, email, postCompany, postAddress, postNumber, paymentMethod} = {...order};
    return (
        <li 
            key = {order.id} 
            className="order"
        >
            <div className="order__block">
                <div className="order__details">
                    <h3> </h3>
                    <p className="order__id"><span> </span><br />#{order.id}</p>
                    <p><span className='order__status'></span><br />{order.status}</p>
                    <p className='order__date'><span> </span><br />{formatDate(order.orderDate.toDate())}</p>
                    {order.ttn && <p><span></span><br /><b>{order.ttn}</b></p>}
                </div>
            </div>
            {order.isFastBuy ? 
                <div className="order__block">
                    <h3> </h3>
                    <div className="order__details">
                        <p><span> </span><br />{phoneNumber}</p>
                    </div>
                </div> : 
                <>
                    <div className="order__block">
                        <h3> </h3>
                        <div className="order__details">
                            <p><span></span><br />{lastName + ' ' + firstName + ' ' + middleName }</p>
                            <p><span> </span><br />{phoneNumber}</p>
                            <p><span> </span><br />{email}</p>
                        </div>
                    </div>
                    <div className="order__block">
                        <h3> </h3>
                        <div className="order__details">
                            <p><span> </span><br /> {paymentMethod === 'card' ? '' : ''}</p>
                            <p><span> </span><br /> {postCompany === 'np' ? ' ' : ""}</p>
                            <p><span> </span><br /> {postAddress}</p>
                            <p><span></span><br /> {postNumber}</p>
                        </div>
                    </div>
                </>}
            <div className="order__block  order__products">
                <h3> </h3>
                <ul className="product-info">
                    <li><span></span><span></span><span>-</span><span></span></li>
                    {order['order'] && order['order'].map(({id, photoUrl, title, amount, price, isDiscount, discPrice, article}) => { 
                        return (
                            <li key={id} style={{position: "relative"}}>
                                <div className='grid-center'><img src={photoUrl} alt={title} /></div>
                                <p>{title}</p>
                                <p>x{amount}</p>
                                <p>{isDiscount ? amount * discPrice : amount * price} </p>
                                <p className="order__article">: {article}</p>
                            </li>
                        )
                    })}
                    <li>
                        <span></span><b>
                        {order['order'] && order['order'].reduce((acc, prod) => 
                            acc += prod.isDiscount ? 
                                prod.discPrice*prod.amount :
                                prod.price*prod.amount, 0)} 
                    </b></li>
                </ul>
            </div>
        </li>
    )
}

export default ModalContentOrdersList;